import React, { Component } from 'react';
import { Consumer } from '../../context';
import {
  Table,
  TableHead,
  TableRow,
  TableBody,
  TableCell,
  Checkbox,
} from '@material-ui/core';
import moment from 'moment';
import 'moment/locale/ru';

moment.locale('ru');

export default class TodoList extends Component {
  isDoneChange = (id, isDone, dispatch) => {
    dispatch({ type: 'EDIT_TODO', payload: { id, isDone } });
  };
  render() {
    const { todos } = this.props;
    return (
      <Consumer>
        {(value) => {
          return (
            <Table className="container">
              <TableHead>
                <TableRow>
                  <TableCell>ID</TableCell>
                  <TableCell>Наименование</TableCell>
                  <TableCell>Дата</TableCell>
                  <TableCell>Статус</TableCell>
                  <TableCell />
                </TableRow>
              </TableHead>
              <TableBody>
                {todos.map((todo) => {
                  return (
                    <TableRow
                      style={{
                        backgroundColor: todo.isDone
                          ? '#E6EE9C'
                          : 'transparent',
                      }}
                      key={todo.id}
                    >
                      <TableCell component="th" scope="row">
                        {todo.id}
                      </TableCell>
                      <TableCell>{todo.title}</TableCell>
                      <TableCell>
                        {moment(todo.deadline).format('LL')}
                      </TableCell>
                      <TableCell padding="checkbox">
                        <Checkbox
                          color="primary"
                          value="isDone"
                          checked={todo.isDone}
                          onChange={this.isDoneChange.bind(
                            this,
                            todo.id,
                            !todo.isDone,
                            value.dispatch
                          )}
                        />
                      </TableCell>
                      <TableCell numeric>реадктировать/удалить</TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          );
        }}
      </Consumer>
    );
  }
}



